h1{
  margin: 4px 0px;
  font-family: Arial, Helvetica, sans-serif;
}

p, a {
  font: none;
  font-style: none;
  font-family: Arial, Helvetica, sans-serif;
  font-weight: bold;
}

.pixel-container{
  background-color: rgb(229, 242, 247);
  border: 4px solid black;
  box-shadow: 8px 8px black;
  padding: 5px;
  display: flex;
  flex-direction: column;
  position: absolute;
  /* temporary attributes for testing */
  
  width: 300px;
  height: 400px;
}

.container-top-bar {
  width: 100%;
  height: 40px;
  display: flex;
  justify-content: flex-end;
  align-items: baseline;
  overflow: hidden;
}

.container-content {
  border: 4px solid black;
  box-shadow: inset 4px 4px rgb(0, 0, 0);
  height: 100%;
  overflow: hidden;
  padding: 8px;
}